/* App.css */

body {
  font-family: 'Arial', sans-serif;
  background-color: #2c3e50; /* Dark blue-gray background */
  color: #ecf0f1; /* Light gray text */
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.App {
  text-align: center;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.2);
  background: linear-gradient(to right, #ff8c00, #3498db); /* Orange and blue gradient */
  width: 90%;
  max-width: 400px;
  margin: 0 auto;
  border: 4px solid #ecf0f1; /* Light gray border */
  animation: borderAnimation 2s infinite alternate;
}

@keyframes borderAnimation {
  0% {
    border-color: #ecf0f1;
  }
  100% {
    border-color: #ff8c00; /* Orange border on animation */
  }
}

h1 {
  color: #ff8c00; /* Orange header */
}

button {
  font-size: 16px;
  padding: 15px 20px;
  margin: 10px 5px;
  cursor: pointer;
  border: none;
  border-radius: 8px;
  background: linear-gradient(to right, #3498db, #2980b9); /* Darker blue gradient */
  color: #ecf0f1;
  transition: background-color 0.3s ease-in-out;
}

button:hover {
  background: linear-gradient(to right, #2980b9, #25678c); /* Darker blue on hover */
}

.counts-container,
.trending-container {
  margin-top: 20px;
}

ul {
  list-style: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

li {
  margin: 10px;
  padding: 15px;
  color: #ecf0f1;
  background: linear-gradient(to right, #3498db, #2980b9); /* Darker blue gradient */
  border-radius: 8px;
  flex: 1 0 30%;
}

.reset-button {
  font-size: 16px;
  padding: 15px 20px;
  margin-top: 20px;
  cursor: pointer;
  border: 4px solid #ecf0f1; /* Light gray border */
  border-radius: 8px;
  background: linear-gradient(to right, #4caf50, #45a049); /* Green gradient */
  color: #fff;
  transition: background-color 0.3s ease-in-out;
}

.reset-button:hover {
  background: linear-gradient(to right, #45a049, #4caf50); /* Darker green on hover */
}
